import { css } from '@emotion/css';
import { Colors } from '../colors';
import { mobileMediaQuery } from '../consts';

export const layoutStyle = (backgroundColor?: Colors): string => css({
  width: '100%',
  height: '100%',
  boxSizing: 'border-box',
  overflow: 'scroll',
  backgroundColor
});

export const layoutContentStyle = (height?: string): string => css({
  width: '100%',
  height,
  boxSizing: 'border-box',
  padding: '0 40px',

  [mobileMediaQuery]: {
    padding: '0 20px'
  }
});
